import React, { Suspense, Component } from "react";
import { withTranslation } from "react-i18next";

const MFP = React.lazy(()=>import("../../../Component/imageComponents/mfP"));
const FDP = React.lazy(()=>import("../../../Component/imageComponents/fdP"));
const PMSP = React.lazy(()=>import("../../../Component/imageComponents/pmsP"));
const P2P = React.lazy(()=>import("../../../Component/imageComponents/p2pP"));
const AIFP = React.lazy(()=>import("../../../Component/imageComponents/aifP"));
const NPSP = React.lazy(()=>import("../../../Component/imageComponents/npsP"));
const BondP = React.lazy(()=>import("../../../Component/imageComponents/bondP"));
const CBP = React.lazy(()=>import("../../../Component/imageComponents/cbP"));

export class productSection extends Component {
  render() {
    const { t } = this.props;

    return (
        <div id="products">
        <div className="container text-center">
          <h2>{t("Home:hOME30")}</h2>
          <div className="row">
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><MFP/></Suspense>
              <a href="/mutual-funds">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME31")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><FDP/></Suspense>
              <a href="/fixed-deposit">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME32")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><PMSP/></Suspense>
              <a href="/pms">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME33")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><P2P/></Suspense>
              <a href="/peer-to-peer-lending">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME34")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><AIFP/></Suspense>
              <a href="/alternative-investment-fund">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME35")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><NPSP/></Suspense>
              <a href="/national-pension-scheme">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME36")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><BondP/></Suspense>
              <a href="/bond">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME37")}
                </button>
              </a>
            </div>
            <div className="col-lg-3 col-md-6 col-12 mb-4">
            <Suspense fallback={<div>Loading....</div>}><CBP/></Suspense>
              <a href="/capital-gain-bond">
                <button id="product-button" type="button" name="button">
                  {t("Home:hOME38")}
                </button>
              </a>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default withTranslation()(productSection);